.CardRoot {
    max-width: 680px;
    border: 1px solid rgba(0, 0, 0, 0.08);
    margin-bottom: 20px;
    box-shadow: none;
    border-radius: 4px;
}
.CardLink {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.CardActionArea {
    display: flex;
    position: relative;
}

.CardContent {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 6px 16px 6px 16px;
}

.CardActions {
    display: flex;
    align-items: center;
    margin-left: -4px;
    margin-right: 6px;
}

.ContentLeft, .ContentRight {
    display: flex;
    flex-flow: column;
}

.ContentLeft {
    flex: 1 1 auto;
    display: flex;
    justify-content: space-between;
}
.ContentRight {
    align-items: flex-end;
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.ContentLeftBottom {
    display: flex;
    align-items: center;
    margin-top: 4px;
}
.ContentRightBottom {
    display: flex;
    flex-flow: column;
    align-items: flex-end;
}

.Intro {
    font-size: 13px;
    padding: 4px 0;
}
.Media {
    width: 240px;
    height: 180px;
}

.ShareButton {
    margin-left: 6px;
}

.Info {
    display: flex;
    margin-top: 4px;
    align-items: center;
}

.Author {
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    margin-right: 8px;
    color: #252525;
}

.Title {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
}

.Written {
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 1;
}
.Time {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
}

.TagContainer {
    display: flex;
}