
.Container {
    display: flex;
    align-items: center;
    background-color: #fff;
    padding: 16px 20px 12px;
}

.Category {
    display: flex;
    flex: 1 0 auto;
    padding-left: 120px;
}

.Category > div {
    margin: 0 30px;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #252525;
}

@media only screen and (max-width: 768px) {
    .Container {
        padding: 16px 16px 12px;
    }
}