.SearchInput {
    display: flex;
}

.SearchInput > input {
    width: 200px;
    height: 32px;
    border-radius: 2px;
    border: solid 1px #dddbda;
    background-color: #ffffff;
    padding: 12px 15px;
    outline: none;  
    font-size: 13px;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1px;
    text-align: left;
    color: #808080;
}

@media only screen and (max-width: 768px) {
    .SearchInput > input {
        width: 160px;
    }
}