
.Container {
    position: relative;
    margin: 0 4px 4px 0;
}
.Author {
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    margin: 2px 4px 2px 0;
}
.TitleRow {
    margin-bottom: 6px;
    max-width: 200px;
}
.Title {
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 1.75;
}

.FlexRow {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin: 3px 0;
}
.AuthorRow, .TagRow {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    overflow-x: hidden;
}
.TagDiv {
    margin: 4px 4px 2px 0; 
    font-size: 10px;
    padding: 4px 6px;
    white-space: nowrap;
}

.BottomRow {
    margin-top: 8px;
}

.Date {
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
}

.Price {
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 1;
}

.Time {
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
}


@media only screen and (max-width: 768px) {

    .Container {
        font-size: 12px;
        width: 160px;
        margin: 0 6px 4px 0;
    }
    .TitleRow {
        margin-bottom: 2px;
        max-width: 160px;
    }
    .Title {
        line-height: 1.5;
    }
    .BottomRow {
        margin-top: 4px;
        font-size: 10px;
    }
    .Time {
        font-size: 10px;
    }
}