.Container {
    display: flex;
    align-items: center;
    z-index: 999;
    position: absolute;
    background: #fff;
    left: 10px;
    padding: 10px 14px;
    border-radius: 3px;
    font-size: 15px;
    letter-spacing: 1px;
    font-weight: 600;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px -1px;
}

.Pos1 {
    bottom: 100px;
}

.Pos2{
    bottom: 40px;
}

@media only screen and (max-width: 768px) {
    .Container {
        font-size: 14px;
        padding: 10px;
    }
}