.Container {
    display: flex;
    align-items: center;
    background-color: #fff;
    padding: 16px 20px 12px;
}

.Container > div {
    font-weight: 600;
    font-size: 16px;
}

.Toggle {
    margin-left: 10px;
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 768px) {
    .Container {
        padding: 16px 20px 12px 2px;
    }
    .Container > div {
        white-space: nowrap;
        text-overflow: ellipsis;
        max-width: calc(100vw - 200px);
        overflow: hidden;
        font-size: 14px;
    }
}