.Tag {
    border-radius: 20px;
    font-family: "Open Sans";
    font-size: 12px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.33;
    letter-spacing: 0.9px;
    text-align: center;
    color: #1b2c32;  
    cursor: pointer;
    opacity: 0.8;
    padding: 4px 8px;
    margin: 4px 4px 0px;
    background-color: rgba(124, 185, 20, .35);
}