.Container {
    display: flex;
    flex-flow: column;
    min-height: 100vh;
    background-color: #fff;
    margin: 0 auto;
}

.Results {
    display: flex;
    flex: 1 1 auto;
    padding-top: 120px;
}
.Results.ResultsWrap {
    flex-wrap: wrap;
}

.SearchBar {
    display: flex;
    flex-flow: column;
    position: fixed;
    z-index: 9;
    width: 100%;
    background: #fff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
}

.Bar {
    z-index: 10;
    display: flex;
    align-items: center;
}

.ResultsList {
    height: 100%;
    min-width: 720px;
    padding: 30px 20px 20px;
}
.ResultsList.ListHidden {
    opacity: 0;
    width: 0;
    min-width: 0;
    padding: 0;
}

.RightRail {
    flex: 1 1 auto;
}

.ResultsMap {
    position: fixed;
    top: 0px;
    left: inherit;
    bottom: auto;
    transform: translateY(0px);
    padding-bottom: 0;
    padding-top: 0;
    height: 100%;
    max-height: 100vh;
    min-height: calc(100vh - 62px);
    padding-top: 176px;
}

@media only screen and (max-width: 768px) {

    .Results {
        flex-flow: column;
        padding-top: 0;
    }
    .RightRail {
        order: 1;
    }
    .SearchBar {
        width: 100%;
        max-height: 65px;
        overflow: hidden;
        flex-flow: row;
    }
    .ResultsMap {
        padding-top: 112px;
    }
}